      <div class="row">
        <div class="col-sm-12 col-md-4">
          <div class="box">
            <span class="logoInit">
              <i class="fas fa-book-reader se-icon"></i>
            </span>
            <h3>Education</h3>
            <p>Education Details and description.</p>
            <div class="linkbtn">
              <router-link to="/education" class="btn btn-danger">
