          <div class="box">
            <span class="logoInit">
              <i class="fas fa-tasks se-icon"></i>
            </span>

            <h3>Project</h3>
            <p>Project details and description.</p>
            <div class="linkbtn">
              <router-link to="/project" class="btn btn-danger">
