<style scoped>
body {
  overflow-x: hidden;
}

.slide {
  position: relative;
  padding: 25vh 10%;
  min-height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  transform-style: inherit;
}
.header {
  text-align: center;
  font-size: 175%;
  color: rgb(31, 35, 247);
  text-shadow: 0 3px 2px rgb(66, 65, 65);
}
#title {
  background-image: url("../assets/rs1.png");
  z-index: 2;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  overflow-x: hidden;
}
#title .text-show {
  top: 10%;
  left: 30%;
  width: 60%;
}
@media screen and (max-width: 560px) {
  #title .text-show {
    font-size: 25px;
  }
  #title .text-show h1 {
    font-size: 30px;
  }
}
.arrow {
  text-align: center;
  margin: 8% 0;
}
.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
