  }
}
.box {
  background-image: linear-gradient(
    to bottom right,
    rgb(15, 136, 250),
    rgb(122, 212, 235)
  );
  padding: 25px;
  margin-top: 40px;
  position: relative;
  margin-bottom: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
