  margin-top: 20px;
  font-weight: 700;
  color: #fff;
}
.box p {
  color: #fff;
  font-weight: 400;
  font-size: 18px;
}
.box:hover .logoInit {
  transform: rotate(360deg);
  transition: transform 1.5s;
}
#about {
  margin-top: 40px;
  padding: 10px;
}

.linkbtn {
  left: 0;
  width: 100%;
  text-align: center;
}
.linkbtn .fas:hover {
  background: #2e5cff;
}

.logoInit {
  position: absolute;
  padding: 15px;
  border-radius: 100%;
  border: 4px solid white;

  top: -40px;
  left: 38%;
  background: rgb(23, 107, 233);
}
</style>




